.wrapper {
  margin: 0 20px;
  background: url('../../../img/background/pagesSecondScreen.jpg');
  background-image: -webkit-image-set(url('../../../img/background/pagesSecondScreen.webp') 1x, url('../../../img/background/pagesSecondScreen.jp2') 1x, url('../../../img/background/pagesSecondScreen.jpg') 1x);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 30px 10px;
  overflow: hidden;
}

.header {
  font-family: 'Caviar Dreams';
  font-size: 22px;
  line-height: 36px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  font-weight: 300;
  margin: 0 0 15px 0;
}

.text {
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  color: #FFFFFF;
  width: 300px;
  margin: 0 auto 90px auto;
}
.img {
  display: block;
  width: 300px;
  height: auto;
  margin: 0 auto;
}

@media(min-width: 1200px) {
  .wrapper {
    margin: 0 70px;
    padding: 30px 0 0 50px;
  }

  .header {
    font-size: 48px;
    line-height: 60px;
    padding-right: 50px;
    margin: 0;
  }
  .information {
    display: flex;
    justify-content: space-between;
  }
  .text {
    font-size: 18px;
    line-height: 30px;
    margin: 50px 28px 0 0;
    width: 38%;
  }
  .img {
    width: 100%;
    height: auto;
    margin: 0;
  }
}
